<template><section><html><head></head><body><h1>CesiumTerrainProvider</h1>
<p>The <code v-pre="">vc-provider-terrain-cesium</code> component is used to load terrain data in Cesium terrain format. If <code v-pre="">url</code> is empty, the CesiumIon online global terrain is loaded by <code v-pre="">Cesium.createWorldTerrain</code> by default.</p>
<h2>Example</h2>
<h3>Load terrain data in Cesium terrain format</h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-layer-imagery>
          <vc-provider-imagery-bingmaps url="https://dev.virtualearth.net" bm-key="AmGu3cvB_g1HbkQErEyvmLc9j0YIGWS7IdOqR7-hQbO8J92Fzrzkhy_bYKSsyoEx" map-style="Aerial"></vc-provider-imagery-bingmaps>
        </vc-layer-imagery>
        <vc-provider-terrain-cesium ref="terrain" :request-water-mask="requestWaterMask"></vc-provider-terrain-cesium>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
